div.doc {
    margin-bottom: 180px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
    max-width: 750px;
    color: rgba(0,0,0,.75);
    font-size: 16px;
    font-family: Lato,Helvetica Neue,Helvetica,sans-serif;
    -webkit-font-variant-ligatures: common-ligatures;
            font-variant-ligatures: common-ligatures;
    line-height: 1.67;
    -webkit-font-smoothing: antialiased;
}

div.doc > div {
    display: block;
}

div.doc > h1 {
    font-size: 2em;
    display: block;
    -webkit-margin-before: 0.67em;
    -webkit-margin-after: 0.67em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    font-weight: bold;
}

div.doc > h1, h2, h3, h4, h5, h6 {
    margin: 1.8em 0;
    line-height: 1.33;
}

div.doc > h1:after, h2:after {
    content: "";
    display: block;
    position: relative;
    top: .33em;
    border-bottom: 1px solid hsla(0,0%,50%,.2);
}

div.doc > p {
    display: block;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    text-align: justify;
}

div.doc > blockquote, dl, p, pre{
    margin: 1.2em 0;
}

div.doc > blockquote {
    color: rgba(0,0,0,.5);
    padding-left: 1.5em;
    border-left: 5px solid rgba(0,0,0,.075);
}

div.doc > code {
    background-color: rgba(0,0,0,.05);
    border-radius: 2px;
    padding: 2px 4px;
}

div.doc > code *, pre *, samp * {
    font-size: inherit;
}

div.doc > code, pre, samp {
    font-family: Roboto Mono,Lucida Sans Typewriter,Lucida Console,monaco,Courrier,monospace;
    font-size: .85em;
}

div.doc > pre {
    all: unset;
    background-color: rgba(0,0,0,.05);
    display: block;
    padding: .5em;
    overflow-x: hidden;
    white-space: pre;
}

